/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layout
--------------------------------------------------------------*/
body {
	margin: 0;
	min-height: 90vh;
}

.flex {
	display: flex;
}

@media screen and (max-width: 700px) {
	.flex {
		flex-wrap: wrap;
	}
	.content-area {
		padding: 1em 1.5em 1.5em;
	}
	.site-content {
		padding: 0;
	}
