	padding: 1em 1em 0;
}

.depth-1.single-comment article {
	padding: 1em 1em .5em;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
embed,
iframe,
object {
	max-width: 100%;
}

a > img {
	display: block;
}

img[src$=".svg"], img[srcset$=".svg"] {
	display: inline;
	vertical-align: middle;
}

.featured-img {
	height: 250px;
	margin: 0;
	object-fit: cover;
	width: 100%;
}

figure {
	background: #fcfcfc;
	border: 1px solid #F3F3F3;
	margin-bottom: 1.5em;
	margin-top: 0;
	padding: 8px;
}

figure figcaption {
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5em;
	padding: 8px 0 0;
	text-align: center;
}

figure img {
	width: 100%;
}

.alignleft {
	float: left;
	margin: 0 1.5em 1.5em 0;
}

.alignright {
	float: right;
	margin: 0 0 1.5em 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33333%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66667%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28571%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11111%;
}

.gallery-caption {
	display: block;
}
